import React, { Component } from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import { Link } from 'react-router';

import '../app.css'

const Page3 = ({ children, location: { pathname } }) => (


    <div className="Image">
        <h1><Link to="/">index</Link></h1>
        <p><Link to="/page3/Animation_1">1</Link></p>
        <p><Link to="/page3/Animation_2">22</Link></p>
        {/* page3 component = "div"    transitionName="example"   */}
        {/*React.cloneElement( this,props.children ||  <div/>,  Animation_1 
            /page3      /page3
              <Link to="/page3/Animation_1">1</Link></p>    "/page3/Animation_1"  
             page1   const key = pathname.split('/')[1] || 'root'      key      
               http://localhost:3333/page3/Animation_1    /page3    
        )*/}
        <ReactCSSTransitionGroup
            component="div" transitionName="example"

            transitionEnterTimeout={500} transitionLeaveTimeout={500}
            >
            {React.cloneElement(children || <div/>, { key: pathname })}
        </ReactCSSTransitionGroup>
    </div>

)




export default Page3;



